<template>
  <div class="detail">
    <!-- 头部 -->
    <div class="header">
      <div class="header_left">
        <van-icon name="arrow-left" size="25px" color="white" @click="back" />
      </div>
      <div class="header_right">
        <van-icon name="cart-o" size="25px" color="white" />
      </div>
    </div>
    <!-- banner层 -->
    <div class="banner">
      <img
        v-if="detaildata.pics == '' "
        class="auto-img"
        :src="require('../assets/images/kzt.png')"
        alt
        srcset
      />
      <van-swipe
        v-else
        @change="onChange"
        class="my-swipe"
        :autoplay="3000"
        indicator-color="white"
      >
        <van-swipe-item v-for="(item,index) in detaildata.pics" :key="index">
          <img class="auto-img" :src="item.pics_mid" alt srcset />
        </van-swipe-item>
        <template #indicator>
          <div class="custom-indicator">{{ current + 1 }}/{{picslength}}</div>
        </template>
      </van-swipe>
    </div>
    <!-- 价格 -->
    <div class="money">
      <span>￥</span>
      {{detaildata.goods_price}}
    </div>
    <!-- 名字 -->
    <div class="name">{{detaildata.goods_name}}</div>
    <!-- 模块添加 -->
    <div class="module-adds">
      <van-row>
        <van-col span="8">快递：15.00</van-col>
        <van-col span="8">月销量343件</van-col>
        <van-col span="8">江苏常州</van-col>
      </van-row>
    </div>
    <!-- 元件添加 -->
    <div class="module-cell">
      <div class="module-cell-list clearfix" v-for="(item,index,i) in moduleCell" :key="index">
        <div class="module-cell-left fl">{{item.names}}</div>
        <div class="module-cell-mid fl">
          <div class="context fl" v-for="(v,index) in item.context">{{v}}</div>
        </div>
        <div class="module-cell-right fr">
          <!-- <van-icon name="arrow" size="13px" color="#999999" /> -->
        </div>
      </div>
      <!-- 套餐选择 -->
      <div class="module-cell-list clearfix" @click="Typeshow = true">
        <div class="module-cell-left fl">套餐</div>
        <div class="module-cell-mid fl">
          <div class="context fl">
            已选
            <div class="fr tc">{{taocan.typeName}}</div>
          </div>
        </div>
        <div class="module-cell-right fr">
          <van-icon name="arrow" size="13px" color="#999999" />
        </div>
      </div>
      <!-- 套餐选择详情 -->
      <van-action-sheet v-model="Typeshow" cancel-text="确定" @cancel="Confirm()" description="套餐选择">
        <div class="content clearfix">
          <div class="clearfix">
            <div class="type_left fl">
              <img
                v-if="this.detaildata.goods_small_logo == 0"
                :src="require('../assets/images/kzt.png')"
                class="auto-img"
              />
              <img v-else :src="detaildata.goods_small_logo" class="auto-img" />
            </div>
            <div class="type_right fl">
              <div class="money fl">
                ￥
                <span>{{detaildata.goods_price}}</span>
              </div>
              <div class="typeSelect fl">
                已选
                <span>{{taocan.typeName}}</span>
              </div>
            </div>
          </div>
          <div class="taoc clearfix">
            <div class="title fl">套餐</div>
            <span
              class="fl"
              :class="{active: item.isActive }"
              v-for="(item,index) in type"
              :key="index"
              @click="toggleType(index)"
            >{{item.name}}</span>
          </div>
          <div class="num clearfix">
            <div class="fl">数量</div>
            <div class="fr">
              <van-stepper theme="round" v-model="taocan.num" button-size="22" disable-input />
            </div>
          </div>
        </div>
      </van-action-sheet>
      <!-- 商品参数 -->
      <div class="module-cell-list clearfix" @click="Attrsdatashow = true">
        <div class="module-cell-left fl">参数</div>
        <div class="module-cell-mid fl">
          <div class="context fl">商品参数</div>
        </div>
        <div class="module-cell-right fr">
          <van-icon name="arrow" size="13px" color="#999999" />
        </div>
      </div>
      <!-- 商品参数详情 -->
      <van-action-sheet v-model="Attrsdatashow" cancel-text="确定" description="基本信息">
        <div class="content clearfix" v-for="(item,index) in attrsdata" :key="index">
          <div class="attrsdata_left fl">{{item.attr_name}}</div>
          <div class="attrsdata_right fl">{{item.attr_value}}</div>
        </div>
      </van-action-sheet>
      <!-- 服务 -->
      <div class="module-cell-list clearfix secolor" @click="Servicedatashow = true">
        <div class="module-cell-left fl">服务</div>
        <div class="module-cell-mid fl">
          <div class="service fl" v-for="(item,index) in servicedata" :key="index">
            <div class="service_img fl">
              <i class="fa fa-check-circle-o"></i>
            </div>
            <span>{{item.name}}</span>
          </div>
        </div>
        <div class="module-cell-right fr">
          <van-icon name="arrow" size="13px" color="#999999" />
        </div>
      </div>
      <!-- 服务详情 -->
      <van-action-sheet v-model="Servicedatashow" cancel-text="确定" description="服务说明">
        <div class="content clearfix" v-for="(item,index) in servicedata" :key="index">
          <div class="servicedata_left fl">
            <i class="fa fa-check-circle-o"></i>
            {{item.name}}
          </div>
          <div class="servicedata_right fl">{{item.title}}</div>
        </div>
      </van-action-sheet>
    </div>
    <!-- 猜你喜欢 -->
    <div class="likes clearfix">
      <van-divider :style="{ color:'black', borderColor: '#B3B3B3', padding: '0 50px'}">猜你喜欢</van-divider>
      <div class="likes_content fl">
        <div
          class="likes_list fl"
          v-for="(item,index) in likes"
          :key="index"
          @click="changeDetail(index,item)"
        >
          <img
            v-if="item.goods_small_logo == 0"
            class="auto-img"
            :src="require('../assets/images/kzt.png')"
            alt
          />
          <img v-else class="auto-img" :src="item.goods_small_logo" alt />
          <span>{{item.goods_name}}</span>
          <div class="likes_money" v-if="item.goods_price == 0">
            ￥
            <a>9999</a>
          </div>
          <div class="likes_money" v-else>
            ￥
            <a>{{item.goods_price}}</a>
          </div>
        </div>
      </div>
    </div>
    <!-- 图文详情 -->
    <div class="graphicdetails">
      <van-divider :style="{ color:'black', borderColor: '#B3B3B3', padding: '0 50px'}">图文详情</van-divider>
      <div class="products">
        <span>商品介绍</span>
      </div>
      <div v-html="Introduce"></div>
    </div>
    <!-- 购物车 -->
    <van-goods-action>
      <van-goods-action-icon icon="wap-home-o" @click="$router.push('/')" text="首页" />
      <van-goods-action-icon
        v-if="this.Detailcart.sc_isActive === true"
        icon="star-o"
        @click="FavoritesAdd(Detailcart)"
        text="收藏"
      />
      <van-goods-action-icon
        v-else
        icon="star"
        color="#ffc900"
        @click="FavoritesAdd(Detailcart)"
        text="收藏"
      />
      <van-goods-action-icon
        color="black"
        icon="cart-o"
        :badge="this.$store.state.favorites.ShopcartList.length"
        text="购物车"
      />
      <van-goods-action-button @click="goPage('Shopcart')" text="立即购买" />
      <van-goods-action-button color="#FC6700" @click="ShopcartListAdd(Detailcart)" text="加入购物车" />
    </van-goods-action>
  </div>
</template>

<script>
import { Toast, Uploader } from "vant";
import "../assets/css/detail.less";
import { createNamespacedHelpers } from "vuex";
const { mapState, mapMutations } = createNamespacedHelpers("favorites");
export default {
  data() {
    return {
      // 截取路由参数
      goods_id: "",
      keywords: "",
      // 商品详情
      detaildata: [],
      // 轮播图
      current: 0,
      picslength: "",
      //猜你喜欢
      productlist: "",
      //方式
      moduleCell: {
        pay: {
          names: "支付",
          context: ["支持", "微信", "支付宝"],
        },
        transport: {
          names: "运费",
          context: ["满98元免基础运费"],
        },
      },
      //套餐选择
      type: [
        { name: "套餐一", isActive: true },
        { name: "套餐二", isActive: false },
      ],
      Typeshow: false,
      taocan: {
        typeName: [],
        // 商品数量
        num: 1,
        sc_isActive: true,
      },
      //确认套餐
      Detailcart: [],
      //商品参数
      attrsdata: [],
      Attrsdatashow: false,
      // 服务
      Servicedatashow: false,
      servicedata: [
        {
          name: "供应商发货",
          title: "由供应商发货，并提供售后服务",
        },
        {
          name: "商品保管说明",
          title:
            "当您购买由小鹅配送的小鹅自营商品（定制、预售、保质期及虚拟产品除外），由于特殊原因需要延迟收货时，小鹅将向您提供一定期限的免费保管服务。",
        },
        {
          name: "闪电发货",
          title:
            "除预订及其他另约的发货情况外，我们将在您付款后次日起的3个工作日内发货，如未按时发货可联系客服申请赔付",
        },
        {
          name: "超时赔付",
          title:
            "保税仓发货商品承诺7个工作日送达，海外直邮商品承诺20个工作日送达，一般贸易商品承诺7个工作日送达，如未按时送达可联系客服申请赔付！",
        },
        {
          name: "7天无理由退货",
          title: "无理由退货，保购物无忧",
        },
      ],

      //猜你喜欢
      likes: [],
      //商品介绍
      Introduce: "",
      total: 0,
    };
  },
  created() {
    // 截取路由参数
    this.Goodsid = this.$route.query.goods_id;
    this.keywords = this.$route.query.keywords;
    this.taocan.typeName = this.type[0].name;
    //
    //
    //获取商品详情数据
    this.detailData(this.Goodsid);
  },
  methods: {
    ...mapMutations(["FavoritesAdd", "ShopcartListAdd"]),

    //获取商品详情数据
    detailData(Goodsid) {
      //获取token
      let tokenString = localStorage.getItem("__Tk");
      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });
      this.axios({
        methods: "GET",
        url: "/goods/detail",
        params: {
          goods_id: Goodsid,
          //商品数量
          num: this.taocan.num,
        },
      })
        .then((result) => {
          this.$toast.clear();
          if (tokenString) {
            if (result.data.meta.status == 200) {
              this.detaildata = result.data.message;
              //如果价钱为0
              if (this.detaildata.goods_price == 0) {
                this.detaildata.goods_price = 9999;
              }

              //图片数组长度
              this.picslength = this.detaildata.pics.length;
              //商品参数数组
              this.attrsdata = this.detaildata.attrs;
              this.Introduce = this.detaildata.goods_introduce;
              //猜你喜欢
              this.keywords.forEach((v, i) => {
                if (this.goods_id == this.keywords[i].goods_id) {
                  this.removed(this.keywords, "goods_id", this.goods_id);
                }
              });

              //猜你喜欢随机排序
              this.likes = this.keywords;
              if (this.likes.length >= 6) {
                this.likes = this.keywords
                  .sort(function () {
                    return Math.random() - 0.5;
                  })
                  .splice(0, 6);
              } else {
                return;
              }
              this.Detailcart = Object.assign(
                {},
                this.Detailcart,
                this.taocan,
                this.detaildata
              );
              let arr = JSON.parse(localStorage.getItem("Favorites"));

              for (let k = 0; k < arr.length; k++) {
                if (arr[k].goods_id == this.Detailcart.goods_id) {
                  this.Detailcart.sc_isActive = !this.Detailcart.sc_isActive;
                  arr[k].sc_isActive == this.Detailcart.sc_isActive;
                }
              }
              this.$store.state.favorites.Favorites = JSON.parse(
                localStorage.getItem("Favorites")
              );
              this.$store.state.favorites.ShopcartList = JSON.parse(
                localStorage.getItem("Shopcart")
              );
            }
          } else {
            //如果token无效，则跳转到登录页面
            this.$router.push({ name: "Login" });
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    //数组去重
    removed(arr, attr, value) {
      //数组，属性，属性值
      var index = 0;
      for (var i in arr) {
        if (arr[i][attr] == value) {
          index = i;
          break;
        }
      }
      arr.splice(index, 1);
    },
    //切换套餐
    toggleType(index) {
      this.type.forEach((v) => {
        v.isActive = false;
      });
      this.type[index].isActive = true;
      this.taocan.typeName = this.type[index].name;
    },
    //套餐确认
    Confirm() {
      this.Detailcart = Object.assign(
        {},
        this.Detailcart,
        this.taocan,
        this.detaildata
      );
    },
    //轮播图
    onChange(index) {
      this.current = index;
    },
    //跳转页面
    goPage(name) {
      this.$router.push({ name });
    },
    //跳转上一页
    back() {
      this.$router.go(-1);
    },
    // 动态修改路由地址
    changeDetail(index, item) {
      this.$router.push({
        path: this.$route.path,
        query: Object.assign({}, this.$route.query, {
          goods_id: this.likes[index].goods_id,
        }),
      });
      this.detailData(this.likes[index].goods_id);
    },
  },
  computed: {
    ...mapState(["Favorites", "ShopcartList"]),
  },
};
</script>

<style lang="less" scoped>
</style>